  methods:{

    callJsonSort:function(){
      this.detail = this.jsonSort(this.detail);
      console.info("排序后：", this.detail);
      console.info("排序后："+ JSON.stringify(this.detail));
    },
    jsonSort:function(_detail_){
       console.info("排序前：", _detail_);
       //排最外层
      _detail_ = this.sortKey(_detail_,"sortId");
      //排子层
       for(let i1=0;i1<_detail_.length;i1++){
         if(_detail_[i1].hasOwnProperty("detail")){
           _detail_[i1].detail = this.jsonSort(_detail_[i1].detail);
           console.info("递归排序：", _detail_[i1].detail);
           //return _detail_[i1];
         }else{
           _detail_ = this.sortKey(_detail_,"sortId");
           console.info("json排序：",_detail_ );
           //return _detail_[i1];

         }
       }
      return _detail_;


    },
    sortKey(array,key){
      return array.sort(function(a,b){
          if(!a.hasOwnProperty(key)){
            a[key]=0;
          }
          if(!b.hasOwnProperty(key)){
            b[key]=0;
          }
          var x = parseInt(a[key]);
          var y = parseInt(b[key]);
          return ((x<y)?-1:(x>y)?1:0)
          // return ((x<y)?1:(x>y)?-1:0)


      })
    },
    //不同源图片下载
    downloadIamge(obj_url, obj_name) {//下载图片地址和图片名
      var image = new Image();
      // 解决跨域 Canvas 污染问题
      image.setAttribute("crossOrigin", "anonymous");
      image.onload = function () {
        var canvas = document.createElement("canvas");
        canvas.width = image.width;
        canvas.height = image.height;
        var context = canvas.getContext("2d");
        context.drawImage(image, 0, 0, image.width, image.height);
        var url = canvas.toDataURL("image/png"); //得到图片的base64编码数据

        var a = document.createElement("a"); // 生成一个a元素
        var event = new MouseEvent("click"); // 创建一个单击事件
        a.download = obj_name || "photo"; // 设置图片名称
        a.href = url; // 将生成的URL设置为a.href属性

        a.dispatchEvent(event); // 触发a的单击事件
      };
      image.src = obj_url;
    },
    playAudio:function(){
      let audio = new Audio();
      audio.src = "http://192.168.3.83:38080/fr/imgserver/admin/mz.mp3";
      audio.play();
    },
