      }
    },
    onChange:function(input){
      console.log("Input changed", input);
      this.money = input;
    },

    onKeyPress:function(button){
      console.log("Button pressed", button);
      if(button==='{enter}'){
        this.showKeyBoard = false;
      }
      if (button === "{shift}" || button === "{lock}") this.handleShift();
    },
    handleShift:function() {
      let currentLayout = this.keyboard.options.layoutName;
      let shiftToggle = currentLayout === "default" ? "shift" : "default";

      this.keyboard.setOptions({
        layoutName: shiftToggle
      });
    },
    setKeyboard() {
      this.keyboard.setOptions({
        layoutName: "default",//
        //theme: "hg-theme-default hg-layout-default myTheme",
        layout: {
          //default: ["1 2 3", "4 5 6", "7 8 9", "0 {bksp} {enter}"],
          default: [
            "1 2 3 4 5 6 7 8 9 0",
            "Q W E R T Y U I O P",
            'A S D F G H J K L {shift}',
            "{bksp} Z X C V B N M {enter}",
          ],
          shift: [
            "http:// https:// / : . {shift}",
            "1 2 3 4 5 6 7 8 9 0",
            "q w e r t y u i o p",
            'a s d f g h j k l',
            "{bksp} z x c v b n m {enter}",
          ]
        },
        buttonTheme: [
          {
            class: "bksp",
            buttons: "{bksp}"
          },
          {
            class: "enter",
            buttons: "{enter}"
          }

        ],
        display: {
          '{bksp}': "删除",
          '{enter}': "确认",
          '{shift}': "shift"
        }
      });
    },
    getKeyBoardPosition(e){
      // let x = e.pageX;
      // let y = e.pageY;
      // var rect = e.target.getBoundingClientRect();
      // let x = rect.left;
      // let y = rect.top;
      // console.info("x:"+x+",y:"+y);
      // this.keyboardPosition.left = (x) +'px';
      // this.keyboardPosition.top = (y) +'px';
      // this.keyboardPosition.width='50%';

      this.showKeyBoard=true;
    },
    hideKeyBoard(e){
      // console.info("e.target:"+  e.target  );
      this.money='';
      this.showKeyBoard=false;
    },
    change:function(_new_ , _old_){
      console.info("新值："+JSON.stringify(_new_) +",旧值"+ JSON.stringify(_old_));
    },
    clickItem:function(item){
      alert(JSON.stringify(item));
      console.info("item:"+JSON.stringify(item));
      console.info("item.id:"+item.id);
      console.info("item.caption:"+item.caption);

    },
    _formatArray_:function( ) {
      this.pic_item1 = this.formatArray(this.pic_item,3);
    },
    /**
     * pic_item :[
     {id:'1',caption:'大米'},
     {id:'2',caption:'豆油'},
     {id:'3',caption:'啤酒'},
     {id:'4',caption:'优惠券'},
     {id:'5',caption:'购物卡'},
     {id:'6',caption:'VIP流量包'},
     {id:'7',caption:'50元话费'},
     ],
     * @param //arr 数组对象
     * @param //num 数字代表新生成的数组中，一个数组中包含几个元素
     * formatArray(this.pic_item,3);
     * @return
     *[
     * [{"id":"1","caption":"大米"},{"id":"2","caption":"豆油"},{"id":"3","caption":"啤酒"}],
     * [{"id":"4","caption":"优惠券"},{"id":"5","caption":"购物卡"},{"id":"6","caption":"VIP流量包"}],
     * [{"id":"7","caption":"50元话费"},{},{}]
     * ]
     * */
    formatArray:function(arr,num) {
      var newArray = new Array(Math.ceil(arr.length / num));
      for(var i =0; i<newArray.length; i++){
        newArray[i] = [];
        for(var j=0;j<num; j++){
          newArray[i][j] = {};
        }
      }
      // for(var i =0; i<arr.length.length; i++){
      for(var i =0; i<arr.length; i++){
        newArray[parseInt(i/num)][i%num]= arr[i];
      }
      console.info("newArray:"+ JSON.stringify(newArray)) ;
      return newArray;
    },

    prev(txt,e) {
      e.preventDefault();
      console.info(""+txt);
      this.$refs.nop.prev();

    },
    next(txt,e) {
      e.preventDefault();
      console.info(""+txt);
      this.$refs.nop.next();

    },

    handleClose(done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done();
          this.onclose();
        })
        .catch(_ => {});
    },
    // 图片缩放
    changeScale(num) {
      num = num || 1;
      this.$refs.cropper.changeScale(num);
    },
    // 左旋转
    rotateLeft() {
      this.$refs.cropper.rotateLeft();
    },
    // 右旋转
    rotateRight() {
      this.$refs.cropper.rotateRight();
    },
    // 预览
    realTime(data) {
      var that = this;
      var dataoption = data;
      this.$refs.cropper.getCropData(data => {
        that.imgsrc = data;
        if (dataoption.w >= 200 || dataoption.h >= 200) {
          if (dataoption.w >= dataoption.h) {
            that.scaleratioXY = 200 / dataoption.w;
          }
          if (dataoption.h > dataoption.w) {
            that.scaleratioXY = 200 / dataoption.h;
          }
        }
      });
    },

    // 选择要上传的图片
    uploadImg(e, num) {
      // 选择要上传的图片
      // this.option.img
      var that = this;
      var file = e.target.files[0];


      if (!/\.(jpg|jpeg|png|JPG|PNG)$/.test(e.target.value)) {
        alert('图片类型必须是.gif,jpeg,jpg,png中的一种');
        return false;
      }
      var reader = new FileReader();
      reader.onload = e => {
        let data;
        if (typeof e.target.result === 'object') {
          // 把Array Buffer转化为blob 如果是base64不需要
          data = window.URL.createObjectURL(new Blob([e.target.result]));
        } else {
          data = e.target.result;
        }
        if (num === 1) {
          that.options.img = data;
          // var dat = {img: data};
          // that.$emit('updateimg', dat);
        } else if (num === 2) {
          this.example2.img = data;
        }
      };
      // 转化为base64
      reader.readAsDataURL(file);
      // 转化为blob
      // reader.readAsArrayBuffer(file)
    },
    // 裁剪
    oncropped(type) {
      var that = this;
      if (type === 'blob') {
        this.$refs.cropper.getCropBlob(data => {
          // var img = window.URL.createObjectURL(data);
          // console.info("图片数据2："+data);
        });
      } else {
        this.$refs.cropper.getCropData(data => {
          var dats = {img: data};
          // console.info("图片数据1："+data);
          // that.$emit('getcropperimg', dats);
          if (this.options.img === '') {
            alert('您还没有选择图片');
          } else {
            //调用上传接口
            // this.onclose();
            this.uploadFileToServer(data);
            this.dialogVisible = false;
            this.options.img = '';
            this.imgsrc = '';
          }
        });
      }
    },

    //打开
    onOpen() {
      this.options.img = '';
      this.imgsrc = '';
      this.dialogVisible = true;
    },
    // base64转成bolb对象
    dataURItoBlob(base64Data) {
      let byteString;
      if (base64Data.split(',')[0].indexOf('base64') >= 0) { byteString = atob(base64Data.split(',')[1]); } else { byteString = unescape(base64Data.split(',')[1]); }
      let mimeString = base64Data.split(',')[0].split(':')[1].split(';')[0];
      let ia = new Uint8Array(byteString.length);
      for (let i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
      }
      return new Blob([ia], { type: mimeString });
    },
    async uploadFileToServer (data) {
      let img = new Image();
      img.src = data;
      img.onload = async () => {
        // let _data = this.compress(img);
        let blob = this.dataURItoBlob(data);
        let formData = new FormData();
        // formData.append('file', blob, this.options.fileInfo.name);
        formData.append('file', blob, this.customGlobal.getFormatDateTimeString("3")+".png");
        this.uploadFileRequest(formData);
      };
    },
    async uploadFileRequest  ( params) {
      const res =  axios({
        method: 'post',
        url: this.customGlobal.serverUrl.orgBaseURI+"/fr/postCallPowerFileUpload?userid="+this.customGlobal.userInfoExt.cui_login_id+"&logic=2",
        data: params,
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        onUploadProgress: function (progressEvent) {
          console.info("progressEvent:"+JSON.stringify(progressEvent));
          // 对原生进度事件的处理
        },

      }).then(function (response) {
        console.info("response:"+JSON.stringify(response));
        if (response.data.status === 'success') {
          console.info("上传成功："+response.data.rawfilename);
        }else{
          console.info("上传失败");
        }
      }).catch(function (error) {
          console.log(error);
      });


    }
  }
}
</script>

